header {
    position: relative;
    height: 50px; }

nav {
    width: 100%;
    height: 50px;
    background-color: #212121; }

.hamburger {
    display: none; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;

    &_link {
        color: #ffffff;
        font-size: 15px;
        font-weight: 300;

        &:hover {
            text-decoration: none;
            color: $main-color; } }

    &_item {
        border-right: 2px dotted black;
        padding-right: 28px;

        &:last-child {
            border-right: none; } } }

.subheader {
    position: absolute;
    width: 100%;
    height: 87px;
    background-color: rgba(0, 0, 0, 0.6);

    &_logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }

    &_official {
        margin-top: 6px;
        font-weight: 100;
        font-size: 13px;
        opacity: 0.5;
        color: #FFFFFF; }

    &_call {
        margin-top: 19px;
        font-weight: 300;
        font-size: 12px;
        text-align: right;
        color: #FFFFFF; }

    &_phone {
        display: block;
        margin-top: 8px;
        font-weight: 400;
        font-size: 22px;
        text-align: right;
        text-transform: uppercase;
        color: #FFFFFF;
        line-height: 12px; }

    &_btn {
        margin-top: 21px;
        width: 172px;
        height: 46px;
        background: linear-gradient(0deg, $main-color 0%, #4CE2FF 100%);
        box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
        border-radius: 4px;
        border: none;
        font-weight: 400;
        font-size: 14px;
        text-transform: uppercase;
        color: #FFFFFF;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
        @include btn_hover; } }


.promo {
    min-height: 800px;
    background: url('../img/bg/uber_bg.jpg') center (center / cover) no-repeat;
    padding: 150px 0 135px 0;

    &_header {
        font-weight: 900;
        font-size: 60px;
        text-transform: uppercase;
        color: #FFFFFF;
        line-height: 60px;
        margin: 0;
        text-align: center; }

    &_subheader {
        font-weight: 700;
        font-size: 36px;
        text-transform: uppercase;
        color: #FFFFFF;
        line-height: 36px;
        margin: 0;
        margin-top: 22px;
        text-align: center; }

    &_descr {
        font-weight: 400;
        font-size: 18px;
        line-height: 24px;
        text-align: center;
        color: #FFFFFF;
        margin-top: 23px; }

    &_btn {
        display: block;
        margin: 116px auto 0 auto;
        width: 259px;
        height: 63px;
        background: linear-gradient(0deg, $main-color 0%, #4CE2FF 100%);
        box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        font-weight: 400;
        font-size: 18px;
        text-transform: uppercase;
        color: #FFFFFF;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
        @include btn_hover; } }

.reasons {
    padding: 71px 0 65px 0; }

.label {
    position: relative;
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;

    &_white {
        background-color: #ffffff;
        color: #000000; } }


.title {
    position: relative;
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;

    &_white {
        color: #ffffff; } }

.subtitle {
    position: relative;
    text-align: center;
    color: #222222;
    font-weight: 400;
    font-size: 17px;
    margin-top: 19px;
    z-index: 2;

    &_white {
        color: #ffffff; } }


.reasons {
    padding: 71px 0 65px 0;

    &_block {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 35px; }

    &_round {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 26px;
        width: 113px;
        height: 113px;
        background-color: $main-color;
        border-radius: 100%; }

    &_descr {
        width: 343px; }

    &_subtitle {
            line-height: 21px;
            color: $main-color;
            font-size: 17px;
            font-weight: 400; }

    &_text {
            color: #202020;
            font-size: 15px;
            font-weight: 300;
            line-height: 21px; } }

