.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url("../img/bg/mobile_bg.jpg") center (center / cover) no-repeat;

    .row {
        margin-top: 32px; }

    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;

        &_1 {
            background: url("../img/uberMobile/main_bg1.jpg") center (center / cover) no-repeat; }

        &_2 {
            background: url("../img/uberMobile/main_bg2.jpg") center (center / cover) no-repeat; }

        &_3 {
            background: url("../img/uberMobile/main_bg3.jpg") center (center / cover) no-repeat; }

        &_4 {
            background: url("../img/uberMobile/main_bg4.jpg") center (center / cover) no-repeat; }

        &_5 {
            background: url("../img/uberMobile/main_bg5.jpg") center (center / cover) no-repeat; }

        &_6 {
            background: url("../img/uberMobile/main_bg6.jpg") center (center / cover) no-repeat; }

        &_subtitle {
            font-weight: 400;
            font-size: 24px;
            line-height: 31px;
            text-align: center;
            text-transform: uppercase;
            color: #FFFFFF; }

        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translate(-50%);
            width: 26px;
            height: 26px;
            background: #000000;
            margin: 0 auto;
            border-radius: 100%;
            cursor: pointer;

            &:before {
                content: '';
                display: block;
                width: 14px;
                height: 2px;
                background-color: $main-color;
                transition: 0.3s all; }

            &:after {
                content: '';
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                display: block;
                height: 14px;
                width: 2px;
                background-color: $main-color;
                transition: 0.3s all; }

            &:hover {
                &:before, &:after {
                    background-color: $second-color; } } } } }


.choice {
    position: relative;
    min-height: 896px;
    padding: 56px 0 137.5px 0;
    background: url("../img/bg/mobile_bg.jpg") center (center / cover) no-repeat;

    &_img {
        position: relative;
        display: block;
        margin: 56px auto 0 auto;
        z-index: 2; }

    &_descr {
        font-weight: 300;
        font-size: 17px;
        line-height: 24px;
        text-align: center;
        color: #222222; }

    .black {
        position: absolute;
        top: 0;
        left: 0;
        height: 499px;
        width: 100%;
        background-color: #070716;
        z-index: 1; } }

.require {
    padding: 66px 0 87px 0;

    &_block {
        margin: 33px 0 33px 0;

        &_nbm {
            margin-bottom: 0px; } }

    &_title {
        font-weight: 400;
        font-size: 22px;
        color: #1CA8C3; }

    &_list {
        list-style-type: none;
        padding-left: 50px;
        margin-top: 22px;

        li {
            position: relative;
            font-size: 17px;
            font-weight: 300;
            line-height: 24px;
            margin-bottom: 10px;

            &:before {
                content: '';
                display: block;
                position: absolute;
                left: -32px;
                top: 3px;
                width: 21px;
                height: 20px;
                background: url('../icons/Rectangle.png') center (center / cover) no-repeat; } } }

    &_descr {
        margin-top: 25px;
        font-weight: 300;
        font-size: 17px;
        line-height: 24px;
        color: #1CA8C3; }

    .warning {
        .require_title {
            color: $second-color; }

        .require_list {
            li {
                &:before {
                    background: url('../icons/LayerRad.png') center (center / cover) no-repeat; } } } } }

.world {
    min-height: 720px;
    position: relative;
    padding: 49px 0;
    background: url("../img/bg/world_bg.jpg") center (center / cover) no-repeat;

    &_link {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 49px;
        text-align: center;
        font-weight: 400;
        font-size: 15px;
        line-height: 68px;
        text-transform: uppercase;
        color: $main-color;

        &hover {
            text-decoration: none;
            color: #FFFFFF; } } }

footer {
    padding: 14px 23px 39px 23px;

    a {
        &:hover {
            text-decoration: none; } } }

.footer {
    &_divider {
        width: 299px;
        height: 4px;
        background-color: #1fbad6;
        margin: 0 auto; }

    &_wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-top: 32px; }

    &_wrapper > div {
        width: 33%;

        &:nth-child(3) {
            display: flex;
            justify-content: flex-end; } }

    &_social {
        width: 160px;
        display: flex;
        justify-content: space-between;

        &_item {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 4px;
            width: 32px;
            height: 32px;
            background-color: #070716;
            border-radius: 100%;

            i {
                color: #fff; } } }

    &_links {
        width: 390px;
        margin: 0 auto;

        &_main {
            display: flex;
            justify-content: space-between;
            padding: 0 10px;

            a {
                color: #070716;
                font-size: 15px;
                font-weight: 300;
                text-transform: uppercase; } }

        &_sub {
            display: flex;
            justify-content: space-between;
            margin-top: 10px;

            a {
                color: #999999;
                font-size: 11px;
                font-weight: 300;
                text-transform: uppercase; } }

        &_lang {
            display: block;
            margin: 0 auto;
            color: #1fbad6;
            font-size: 11px;
            font-weight: 400;
            margin-top: 19px;
            text-transform: uppercase;
            text-align: center; } } }

.xs-visible {
    display: none; }
